* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.main {
  font-family: 'Open Sans', sans-serif;
}

.landing-page h3 {
  color: #6c63ff;
  cursor: pointer;
  transition: .3s;
}

.landing-page ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.linker {
  text-decoration: none;
  color: blue;
  font-size: 20px;
  animation: lin 1.5s ease-in-out forwards;
}

@keyframes lin {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

.head {
  background-color: #CDFADB;
  margin-top: -1%;
}

.heading-animation {
  animation: fade 2.5s ease-in-out forwards;
}

@keyframes fade {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate {
  animation: fadeIn 3.5s ease-in-out forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.sec-0 {
  margin-top: -1%;
  background-color: #CDFADB;
}

/* Small */
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

.landing-page header {
  min-height: 80px;
  display: flex;
}

@media (max-width: 767px) {
  .landing-page header {
    min-height: auto;
    display: initial;
  }
}

.landing-page header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 767px) {
  .landing-page header .container {
    flex-direction: column;
    justify-content: center;
  }
}

.landing-page header .logo {
  color: #5d5d5d;
  font-style: italic;
  text-transform: uppercase;
  font-size: 20px;
}

@media (max-width: 767px) {
  .landing-page header .logo {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.landing-page header .links {
  display: flex;
  align-items: center;
}

@media (max-width: 767px) {
  .landing-page header .links {
    text-align: center;
    gap: 10px;
  }
}

.landing-page header .links li {
  margin-left: 30px;
  color: #d6b12a;
  cursor: pointer;
  transition: .3s;
  border-radius: 20px;
  padding: 5px;
}

@media (max-width: 767px) {
  .landing-page header .links li {
    margin-left: auto;
  }
}

.landing-page header .links li:last-child {
  border-radius: 100px;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #6c63ff;
}

.landing-page header .links li:last-child:hover {
  background-color: #d6b12a;
}

.landing-page header .links li:not(:last-child):hover {
  color: #6c63ff;
  background-color: #d6b12a;
}


@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.image-animation {
  opacity: 0;
  transition: opacity 1s ease;
}

.image-animation.animating {
  opacity: 1;
  animation: slideInFromLeft 1s ease-in-out forwards;
}

.landing-page .content .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 140px;
  min-height: calc(100vh - 80px);
}

@media (max-width: 767px) {
  .landing-page .content .container {
    gap: 80;
    justify-content: center;
    flex-direction: column-reverse;
  }
}

@media (max-width: 767px) {
  .landing-page .content .info {
    text-align: center;
    margin-bottom: 15px
  }
}

.landing-page .content .info h1 {
  color: #5d5d5d;
  font-size: 44px;
}

.landing-page .content .info p {
  font-size: 20px;
  color: #5d5d5d;
}

.landing-page .content .info button {
  border-radius: 100px;
  padding: 10px 20px;
  background-color: #6c63ff;
}

.landing-page .content .info button:hover {
  background-color: #d6b12a;
}

.logi {
  font-size: 22px;
  color: #ffffff;
  text-decoration: none;
  cursor: pointer;
  transition: .3s;
}

.logi:hover {
  color: #6c63ff;
}

.landing-page .content .image img {
  height: 400px;
  margin-bottom: 20%;
}

.sec-1 {
  display: flex;
  align-items: center;
  text-align: left;
  background-color: #F6FDC3;
  height: 100vh;
  overflow: hidden;
}

.landing-page .sec-1 .img {
  margin-bottom: 20px;
}

.landing-page .sec-1 .img img {
  margin-top: 28%;
  margin-bottom: 28%;
  margin-left: 30%;
  height: 350px;
  width: auto;
}

.landing-page .sec-1 p {
  font-size: 24px;
  margin-left: 20%;
  color: #5d5d5d;
}

@keyframes slideInFromRight {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.text-animation {
  opacity: 0;
  transition: opacity 1s ease;
}

.text-animation.animatin {
  opacity: 1;
  animation: slideInFromRight 1s ease-in-out forwards;
}


.landing-page .sec-2 {
  background-color: #FFCF96;
}

.landing-page .sec-2 h2 {
  padding-top: 5%;
  margin-bottom: 8%;
  font-size: 44px;
  text-align: center;
  color: #FF8080;
}

@keyframes pop {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.h2-animation {
  opacity: 0;
  transition: opacity 1s ease;
}

.h2-animation.animatin-1 {
  opacity: 1;
  animation: pop 2s ease-in-out forwards;
}

.feature-container {
  display: flex;
  padding-top: 1%;
  padding-bottom: 10%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.feature {
  width: 300px;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 10px;
  opacity: 0;
  transition: 2s ease;
}

.feature.animatin-2 {
  opacity: 1;
  animation: leftside 2s ease-in-out forwards;
}

@keyframes leftside {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.feature.animatin-3 {
  opacity: 1;
  animation: rightside 2s ease-in-out forwards;
}

@keyframes rightside {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

